/*<h7 class="backLinkPeople"><a onClick={() => { window.history.go(-1); }} class="whiteLink">&#10094; BACK TO PEOPLE</a></h7>*/

export default function Undergraduate()
{
    return (
        <>
            <h7 class="backLinkPeople"><a href="javascript:history.back()" class="whiteLink">&#10094; BACK TO PEOPLE</a></h7>
            <img class="imageLoadDelay" id="undergraduateMembersPage" src="IMG_1599.jpg" alt="Undergraduate Members"/>
            <div class="peopleContainer peopleFlex">
                <h14>Current Members</h14>
            </div>
            <div class="memberGrid">
                {picturedFraternityMembers.map((portrait) => (
                    <div class="member">
                        <Portrait name={portrait.name} image={portrait.image} />
                    </div>
                ))}
            </div>
            <div class="peopleContainer peopleFlex">
                <h15>Not pictured</h15>
            </div>
            <div class="nameGrid">
                {unpicturedFraternityMembers.map((names) => (
                    <div class="name">
                        <Names name={names.name}/>
                    </div>
                ))}
            </div>
                <div class="socialMediaGrid">
                    <div class="socialMedia1">
                    <h7>
                        <a href="https://www.facebook.com/uclaaxs/" class="hover-underline-animation">FACEBOOK</a>
                    </h7>
                    </div>
                    <div class="socialMedia2">
                        <h7>
                            <a href="https://www.instagram.com/uclaaxs/" class="hover-underline-animation">INSTAGRAM</a>
                        </h7>
                    </div>
                </div>
        </>
        
    )
}